.App {
  display: flex;
  flex-direction: column;
}

.App .Content {
  width: 100%;
  height: 100vh;
  background-image: url("./image/background/116387.jpg");
}

.App .Content {
	color: #000;

  display: flex;
  flex-direction: column;
}

.App .Content h1 {
  text-align: left;
	margin-top: 20vh;
	margin-left: 10vh;
  font-size: 10vh;
}

.App .Content p {
  color: var(--color-text-base);
  text-align: center;
  font-size: 40px;
  margin-top: 5;
  margin-bottom: 0;
}

.App .Content div {
  display: flex;
	flex-direction: column;
}

.App .Content div > a {
  width: 40rem;
  height: 7.6rem;
  background: var(--color-secondary);
  color: var(--color-button-text);
  border: 0;
  border-radius: 4rem;
  text-decoration: none;
  cursor: pointer;
  font: 700 2.5rem Archivo;
  display: flex;
  align-items: center;
  justify-content: space-evenly;

  transition: 0.2s;
	margin-top: 20vh;
	margin-left: 10vh;

}

.App .Content div > a:hover {
  background: var(--color-secondary-dark);
}
